import React, { Component } from "react";
import Header from "../elements/header";
import Sidebar from "../elements/sidebar";
import { Link, Redirect } from "react-router-dom";
import { createCustomer } from "../Service/customersService";

export default class AddPage extends Component {
  state = {
    redirect: false,
    toDashboard: false,
    isLoading: false,
  };

  handleSubmit = async (event) => {
    event.preventDefault();
    this.setState({ isLoading: true });

    let obj = {};
    obj.name = document.getElementById("inputName").value;
    obj.country = document.getElementById("inputCountry").value;
    obj.address = document.getElementById("inputAddress").value;
    obj.phone = document.getElementById("inputPhone").value;
    obj.businessYearDate = document.getElementById(
      "inputBusinessYearDate"
    ).value;

    let response = await createCustomer(obj);
    if (response) {
      this.props.history.push("/customers");
    } else {
      alert("There is an Error while creating customer");
    }
  };

  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to="/dashboard" />;
    }
  };

  render() {
    const isLoading = this.state.isLoading;
    if (this.state.toDashboard === true) {
      return <Redirect to="/" />;
    }
    return (
      <div>
        <Header />
        <div id="wrapper">
          <Sidebar></Sidebar>
          <div id="content-wrapper">
            <div className="container-fluid">
              <ol className="breadcrumb">
                <li className="breadcrumb-item">
                  <Link to={"/dashboard"}>Dashboard</Link>
                </li>
                <li className="breadcrumb-item active">Add</li>
              </ol>
            </div>
            <div className="container-fluid">
              <div className="card mx-auto">
                <div className="card-header">Add Customer</div>
                <div className="card-body">
                  <form onSubmit={this.handleSubmit}>
                    <div className="form-group">
                      <div className="form-row">
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="text"
                              id="inputName"
                              className="form-control"
                              placeholder="Enter name"
                              required="required"
                              autoFocus="autofocus"
                            />
                            <label htmlFor="inputName">Enter name</label>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="text"
                              id="inputCountry"
                              className="form-control"
                              placeholder="Enter Country"
                              required="required"
                            />
                            <label htmlFor="inputCountry">Enter Country </label>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group">
                      <div className="form-row">
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="text"
                              id="inputAddress"
                              className="form-control"
                              placeholder="Enter Address"
                              required="required"
                            />
                            <label htmlFor="inputAddress">Enter Address</label>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="number"
                              id="inputPhone"
                              className="form-control"
                              placeholder="Enter Phone"
                              required="required"
                            />
                            <label htmlFor="inputPhone">Enter Phone</label>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="form-group">
                      <div className="form-row">
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="date"
                              id="inputBusinessYearDate"
                              className="form-control"
                              placeholder="Enter Business Year Date"
                              required="required"
                            />
                            <label htmlFor="inputBusinessYearDate">
                              Business Year Date
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>

                    <p className="h4 py-3">Sub Customer</p>

                    <div className="form-group">
                      <div className="form-row">
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="text"
                              id="inputContactName"
                              className="form-control"
                              placeholder="Enter Contact Name"
                            />
                            <label htmlFor="inputContactName">
                              Contact Name
                            </label>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="text"
                              id="inputContactDesignation"
                              className="form-control"
                              placeholder="Enter Contact Designation"
                            />
                            <label htmlFor="inputContactDesignation">
                              Contact Designation
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="form-group">
                      <div className="form-row">
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="number"
                              id="inputContactPhoneNumber"
                              className="form-control"
                              placeholder="Enter Contact Phone Number"
                            />
                            <label htmlFor="inputContactPhoneNumber">
                              Contact Phone Number
                            </label>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-label-group">
                            <input
                              type="email"
                              id="inputContactEmail"
                              className="form-control"
                              placeholder="Enter Contact Email"
                            />
                            <label htmlFor="inputContactEmail">
                              Contact Email
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>
                    <button
                      className="btn btn-primary btn-block"
                      type="submit"
                      disabled={this.state.isLoading ? true : false}
                    >
                      Add&nbsp;&nbsp;&nbsp;
                      {isLoading ? (
                        <span
                          className="spinner-border spinner-border-sm"
                          role="status"
                          aria-hidden="true"
                        ></span>
                      ) : (
                        <span></span>
                      )}
                    </button>
                  </form>
                  {this.renderRedirect()}
                </div>
              </div>
            </div>

            <footer className="sticky-footer">
              <div className="container my-auto">
                <div className="copyright text-center my-auto">
                  <span>
                    Copyright © The Great Solutions{" "}
                    <span>{new Date().getFullYear()}</span>
                  </span>
                </div>
              </div>
            </footer>
          </div>
        </div>
      </div>
    );
  }
}
