import React, { Component } from "react";
import Header from "../elements/header";
import Sidebar from "../elements/sidebar";
import { Link, Redirect } from "react-router-dom";
import { findAllArticles, deleteArticle } from "../Service/articlesService";

export default class Index extends Component {
  state = {
    articles: [],
    toDashboard: false,
    isLoading: false,
  };
   _arrayBufferToBase64( buffer ) {
    var binary = '';
    var bytes = new Uint8Array( buffer );
    var len = bytes.byteLength;
    for (var i = 0; i < len; i++) {
        binary += String.fromCharCode( bytes[ i ] );
    }
    return window.btoa( binary );
}
  async componentDidMount() {
    let resp = await findAllArticles();
    resp = resp.reverse();
    resp.forEach((item, index)=>{
      if(item.techPackImage !== '')
      {
       let base64 = this._arrayBufferToBase64(item.techPackImage.data.data)
       resp[index].techPackImage = base64;
      }
    })
    this.setState({ articles: resp });
  }

  handleClickDelete = async (event) => {
    if (window.confirm("Are you sure you wanna delete?")) {
      let response = await deleteArticle(event.target.value);
      if (response) {
        window.location.reload(false);
      } else {
        alert("Error while deleting...");
      }
    }
  };

  render() {
    if (this.state.toDashboard === true) {
      return <Redirect to="/" />;
    }
    return (
      <div>
        <Header />
        <div id="wrapper">
          <Sidebar />
          <div id="content-wrapper">
            <div className="container-fluid">
              <ol className="breadcrumb">
                <li className="breadcrumb-item">
                  <Link to={"/dashboard"}>Dashboard</Link>
                </li>
                <li className="breadcrumb-item active">ARTICLES MANAGEMENT</li>
                <li className="ml-auto">
                  <Link to={"add-article"}>Add Article</Link>
                </li>
              </ol>
              <div className="card mb-3">
                <div className="card-header">
                  <i className="fas fa-table"></i>
                  &nbsp;&nbsp;Articles List
                </div>
                <div className="card-body">
                  <table className="table table-bordered">
                    <thead>
                      <tr>
                        <th>Artical No.</th>
                        <th>Customer</th>
                        <th>Teck Pack Image</th>
                        <th>Color Count</th>
                        <th>Created At</th>
                        <th className="text-center">Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.articles.map((articles, index) => (
                        <tr key={articles._id}>
                          <td>{articles.itemNumber}</td>
                          <td>{articles.userName}</td>
                          <td><img src={articles.techPackImage !== '' ? "data:image/jpeg;base64," + articles.techPackImage : ""} width={100} height={100} alt="" /></td>
                          <td>{articles.colorCount}</td>
                          <td>{articles.createdAt}</td>
                          <td className="text-center">
                            <Link
                              className="btn btn-sm btn-info"
                              to={{
                                pathname: "edit-article",
                                search: "?id=" + articles._id,
                              }}
                            >
                              Edit
                            </Link>
                            &nbsp; | &nbsp;
                            <button
                              value={articles._id}
                              className="btn btn-sm btn-danger"
                              onClick={this.handleClickDelete}
                            >
                              Delete
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
                <div className="card-footer small text-muted">
                  Updated yesterday at 11:59 PM
                </div>
              </div>
            </div>
            <footer className="sticky-footer">
              <div className="container my-auto">
                <div className="copyright text-center my-auto">
                  Copyright © The Great Solutions{" "}
                  <span>{new Date().getFullYear()}</span>
                </div>
              </div>
            </footer>
          </div>
        </div>
      </div>
    );
  }
}
