import React, { Component } from "react";
import Header from "../elements/header";
import Sidebar from "../elements/sidebar";
import { Link, Redirect } from "react-router-dom";
import { findAllNotifications } from "../Service/notificationService";
import { startOfDay } from "date-fns";

export default class Index extends Component {
  state = {
    notifications: [],
    toDashboard: false,
    isLoading: false,
  };

  async componentDidMount() {
    let resp = await findAllNotifications();
    if (resp === "No Notifications") {
      alert("No Notifications");
    } else {
      this.setState({ notifications: resp });
    }
  }

  checkDelivery(articleDate, currentDate) {
    if (articleDate === currentDate) {
      return true;
    } else {
      return false;
    }
  }

  render() {
    if (this.state.toDashboard === true) {
      return <Redirect to="/" />;
    }
    return (
      <div>
        <Header />
        <div id="wrapper">
          <Sidebar />
          <div id="content-wrapper">
            <div className="container-fluid">
              <ol className="breadcrumb">
                <li className="breadcrumb-item">
                  <Link to={"/dashboard"}>Dashboard</Link>
                </li>
                <li className="breadcrumb-item active">Notifications</li>
              </ol>
              <div className="card mb-3">
                <div className="card-header">
                  <i className="fas fa-table"></i>
                  &nbsp;&nbsp;Notifications
                </div>
                <div className="card-body">
                  <table className="table table-bordered">
                    <thead>
                      <tr>
                        <th>Article No.</th>
                        <th>Delivery Date</th>
                        <th>Comment</th>
                        <th>STD</th>
                        <th>Created At</th>
                        <th>Customer</th>
                        <th>Fit Sample</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.notifications.map((notification, index) => (
                        <tr key={notification.itemNo}>
                          <td>{index + 1}</td>
                          <td
                            className={
                              notification.isArticleDelivered
                                ? "bg-success"
                                : this.checkDelivery(
                                    startOfDay(
                                      new Date(
                                        notification.deliveryDate
                                      ).setDate(
                                        new Date(
                                          notification.deliveryDate
                                        ).getDate() + 7
                                      )
                                    ),
                                    startOfDay(new Date())
                                  ) === false
                                ? "bg-danger"
                                : "bg-light"
                            }
                          >
                            <span className="text-white">
                              {notification.deliveryDate}
                            </span>
                          </td>
                          <td></td>
                          <td>
                            {notification.isSTDFileReceived ? (
                              <i class="fas fa-fw fa-check"></i>
                            ) : (
                              <i class="fas fa-fw fa-times"></i>
                            )}
                          </td>
                          <td>{notification.createdAt}</td>
                          <td className="text-center">
                            {notification.userName}
                          </td>
                          <td className="text-center">
                            {notification.isFitSample ? (
                              <i className="fas fa-fw fa-check"></i>
                            ) : (
                              <i className="fas fa-fw fa-times"></i>
                            )}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
                <div className="card-footer small text-muted">
                  Updated yesterday at 11:59 PM
                </div>
              </div>
            </div>
            <footer className="sticky-footer">
              <div className="container my-auto">
                <div className="copyright text-center my-auto">
                  Copyright © The Great Solutions
                  <span>{new Date().getFullYear()}</span>
                </div>
              </div>
            </footer>
          </div>
        </div>
      </div>
    );
  }
}
